import React from "react";
import Textheading from "../textheading/Textheading";
import Location from "./Location";
import { useDispatch, useSelector } from "react-redux";
import Cartproduct from "./Cartproduct";
import Datetime from "./Datetime";
import Proceed from "./Proceed";
import { removeFromCart } from "../../redux/CartSlice";

function Cart({ cart, handleCartClose }) {
  const dispatch = useDispatch();

  const handleDelete = () => {
    dispatch(removeFromCart());
  };

  const productDetails = useSelector((state) => state?.cart?.list || []);

  const totalPrice = useSelector((state) => state.cart.list[0]?.total);

  return (
    <section
      className={`w-full h-full z-50 flex justify-end bg-black bg-opacity-50 fixed top-0 right-0 transition-all duration-500 ${
        cart ? "opacity-100 visible" : "opacity-0 invisible"
      }`}
    >
      <div
        className={`bg-black bg-opacity-50 w-[25%] -xl:w-[35%] -lg:w-[46%] -md:w-full flex relative transition-transform duration-500 transform ${
          cart ? "translate-x-0" : "translate-x-full"
        }`}
      >
        <div
          className="text-white -md:text-black text-5xl cursor-pointer absolute -left-12 -md:-left-0 -md:right-[-22rem] -md:-top-[2px]"
          onClick={handleCartClose}
        >
          <ion-icon name="close-circle-outline"></ion-icon>
        </div>
        <div className="flex flex-col bg-white">
          <Textheading heading="Cart" margin="my-2 mx-4" />
          <hr />
          <div className="flex flex-col h-[90%] overflow-y-auto items-center gap-2 p-4">
            {productDetails.map((item, index) => (
              <div
                key={index}
                className="flex flex-col  pb-4 justify-center overflow-hidden w-full border border-solid rounded-xl border-zinc-200"
              >
                <Location location={item?.deliveryLocation} />
                <div className="px-4">
                  <Cartproduct
                    handleDelete={handleDelete}
                    image={item?.productImage}
                    name={item?.productName}
                    newPrice={item?.newPrice} // Ensure correct case for newPrice
                    ogPrice={item?.originalPrice} // Ensure correct case for originalPrice
                    discount={item?.discount}
                    quantity={item?.quantity}
                  />
                  <Datetime
                    dateSlot={
                      item?.shippingDetails?.deliveryDate?.fullDeliveryDate
                    } // Ensure this path is correct
                    timeSlot={item?.shippingDetails?.deliveryTimeSlot}
                  />
                </div>
              </div>
            ))}
          </div>
          <div>
            <Proceed total={totalPrice} />
          </div>
        </div>
      </div>
    </section>
  );
}

export default Cart;
