import React from "react";

function Deleteproduct({ handleDelete, quantity }) {
  return (
    <div className="w-[35%] text-green-800 rounded-lg flex justify-between px-2 items-center border border-solid border-lime-700">
      <div className="hover:cursor-pointer" onClick={handleDelete}>
        <ion-icon name="trash-outline"></ion-icon>
      </div>
      <span>{quantity}</span>
      <ion-icon name="add-outline"></ion-icon>
    </div>
  );
}

export default Deleteproduct;
